import React from "react";
import Link from "gatsby-link";

const ProjectCard = ({ thumbnail, title, description }) => (
  <div
    style={{
      borderRadius: "4px",
      width: 300,
      height: 450,
      padding: 0,
      margin: 3,
      boxShadow: "0 2px 5px rgba(0, 0, 0, 0.2)"
    }}
  >
  <div
    style={{
      minWidth: 300,
      minHeight: 300,
    }}
    >
    <img
      src={thumbnail}
      alt="Preview"
      style={{
        borderRadius: "4px 4px 0px 0px",
        margin: 0,
        maxWidth: "100%",
        height: 300,
        objectFit: "none"
      }}
    />
  </div>
  <h6 style={{ paddingLeft: 10, paddingTop: 10 }}>{title}</h6>
    <p style={{ color: "gray", paddingLeft: 10 }}>
      {description}
    </p>
  </div>
);

export default ProjectCard;



